body {
  overflow-y: hidden; /* Hide vertical scrollbar */
  overflow-x: hidden; /* Hide horizontal scrollbar */
}

@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');

.nui-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;

  justify-content: center;

  overflow: hidden;

  background: radial-gradient(50% 50% at 50% 50%, rgba(43, 57, 98, 0.15) 0%, rgba(36, 39, 63, 0.9) 100%);
  

  .background {

    $total-slides: 14;
    $interval: 12s;

    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    .slide {

      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
      opacity: 0;
      transform: scale(1.2);
      animation: kenburns $total-slides * $interval linear infinite;

      @for $i from 1 through $total-slides {
        &:nth-child(#{$i}) {
          animation-delay: $interval * ($i - 1);
        }
      }

    }
  }

  .player-wrapper {
    z-index: -1;
    display: none;
    object-fit: cover;
  }

  .hexagons {

    width: 100%;

    .one { 
      position: absolute;
      top: 0;
      left: 0;
    }

    .two {
      position: absolute;
      bottom: 0;
      left: 0;
    }

    .three {
      position: absolute;
      bottom: 0;
      right: 85px;
    }

    .four {
      position: absolute;
      top: -25px;
      right: 0;
    }

