        padding-left: 8px;
        padding-right: 15px;
        padding-top: 8px;
        padding-bottom: 8px;

        .union {
          pointer-events: none;
          z-index: 1;
          position: absolute;
        }

        .circlelogo {
          width: 30px;
          height: 30px;
          z-index: 1;
          position: absolute;
          display: block;
          left: 50%;
          top: 50%;
          transform: translate(-50%,-50%);
          transition: transform 1.5s ease-in-out;
        }

        .hoverartwork {
          transform: rotate(52deg);
          transition: transform 1.5s ease-in-out;
          width: 100%;
          height: 100%;
        }

        .artwork {
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          transition: transform 1.5s ease-in-out;
        }

        img {
          border-radius: 90px;
          width: 100px;
          height: 100px;
          object-fit: contain;
          
        }
      }

      color: #FFFFFF;

      .text {
        display: flex;
        flex-direction: column;

        font-family: 'Oswald', sans-serif;
        font-style: normal;
        font-weight: normal;    

        padding-right: 16px;

        .title {
          transition: all 0.5s ease-in-out;
          font-size: 24px;

          &:hover {
            color: #51F1EE
          }

        }

        .artist {
          transition: all 0.5s ease-in-out;
          font-size: 18px;

          &:hover {
            color: #51F1EE
          }

        }

      }

    }

    .right {
      background: rgba(255, 255, 255, 0.03);
      border-radius: 8px;

      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;

      width: 176px;

      .topcontrols {
        width: 128px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .backward, .forward, .pause {
          transition: all 0.3s ease-in-out;
          &:hover {
            color: #ffffff
          }
        }
        
        margin-top: 20px;
      }

      .bottomcontrols {

        margin-bottom: 20px;
        width: 130px;

        display: flex;
        align-items: center;
        justify-content: space-between;

        .volume {
          transition: all 0.3s ease-in-out;
          &:hover {
            color: #ffffff
          }
        }

        .volumebar {

          -webkit-appearance: none;  /* Override default CSS styles */
          appearance: none;

          border-radius: 4px;

          width: 100%;

          margin-left: 10px;

          height: 30%;
          background: linear-gradient(to right, #51F1EE 0%, #51F1EE 50%, rgba(81, 241, 238, 0.2) 50%, #51F1EE 100%);
        

          .percentage {
            border-radius: 4px;
            position: absolute;
            width: 5%;
            height: 5%;
            background-color: #51F1EE;
          }

          &::-webkit-slider-thumb {
            -webkit-appearance: none; /* Override default look */
            appearance: none;
            width: 13px; /* Set a specific slider handle width */
            height: 13px; /* Slider handle height */
            border-radius: 50%; 
            background: #51F1EE; /* Green background */
          }

        }
      
        

      }

    }

  }

